import React, {Component} from 'react';
import Navigation from '../components/Navigation/Navigation';
import SignIn from '../components/SignIn/SignIn';
import Register from '../components/Register/Register';
import Logo from '../components/Logo/Logo';
import ImageLinkForm from '../components/ImageLinkForm/ImageLinkForm';
import Rank from '../components/Rank/Rank'
import FaceRecognition from '../components/FaceRecognition/FaceRecognition';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai';
import '../styles/App.css';

const app = new Clarifai.App({
  apiKey: 'a4fe351bd53545eaaf97109e17f80689'
 });
 
const particlesOptions = {
  particles: {
    line_linked: {
      shadow: {
        enable: true,
        color: "#3CA9D1",
        blur: 5
      }
    }
  }
}

class App extends Component{
  constructor(){
    super();
    this.state = {
      url:'',
      imageFromUrl: '',
      box: {}, //top_row, bottom_row, left_col, right_col these are properties of bounding box
      route: 'signin'
    }
  }
// this function is called based on the input we get from CLARIFAI
  calculateFaceLocation = (data) =>{
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;  //bounding_box is a percentage of the image
    const image = document.getElementById('inputimage');
    const width = Number(image.width); // grabbing the height and width of the image
    const height = Number(image.height); // making a string number
    return{
      leftCol: clarifaiFace.left_col * width,
      topRow: clarifaiFace.top_row * height,
      rightCol: width - (clarifaiFace.right_col * width),
      bottomRow: height - (clarifaiFace.bottom_row * height)
    }
  }

  displayFaceBox = (box) =>{
    console.log(box);
    this.setState({box: box})
  }
  enteringUrl = (event) =>{
   this.setState({url: event.target.value});
  }

  detecting = () =>{
    this.setState({imageFromUrl: this.state.url})
    console.log('click')
    // using the API Clarifai
    app.models  //clarifai.Model
      .predict(Clarifai.FACE_DETECT_MODEL, this.state.url)
      .then(response => this.displayFaceBox(this.calculateFaceLocation(response)))
      .catch(err => console.log(err)); 
  }

  changingRoute = (route) =>{
    this.setState({route: route})
  }

  render(){
    return (
      <div className="App">
        <Particles className="particles" 
                  params={particlesOptions} 
        />
        { this.state.route === 'home'?        
          <div>
            <Navigation changingRoute={this.changingRoute}/>
            <Logo/>
            <Rank/>
            <ImageLinkForm 
              enteringUrl={this.enteringUrl} 
              detecting={this.detecting}
            />
            <FaceRecognition 
              box={this.state.box} 
              imageFromUrl={this.state.imageFromUrl}
            />
          </div>
          : (
            this.state.route === 'signin'
            ? <SignIn changingRoute={this.changingRoute}/>
            : <Register changingRoute={this.changingRoute}/>
          )
        }
      </div>
    );
  }
}

export default App;
